body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 100%;
  margin: 10px 0
}

.img-container {
  width: 200px;
  margin: auto;
}

.abt h3 {
  margin: 18px auto 0;
}

.abt ul li {
  display: block;
  padding: 12px 10px;
}

@media screen and (max-width: 769px) {
  .img-container {
    width: 80%;
  }
}
#home {
  display: grid;
  grid-template-columns: 15% 1fr 1fr 15%;
  grid-template-rows: 359px 0px 300px 300px;
}

#hero {
  grid-column: 1 / 5;
  grid-row: 1 / 2;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#gathering-info {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  background-color: hsla(0, 0%, 100%, 0.93);
  border: 1px solid #333;
  border-right: 0;
  -webkit-transition-duration: 0.7s;
          transition-duration: 0.7s;
}

#mission-info {
  grid-column: 3 / 5;
  grid-row: 3 / 4;
  background-color: hsla(0, 0%, 100%, 0.93);
  color: #000;
  border: 1px solid #333;
  border-left: 0;
  -webkit-transition-duration: 0.7s;
          transition-duration: 0.7s;
}

#gathering-info:hover,
#mission-info:hover {
  background-color: #fff;
}

#giving-info {
  grid-column: 2 / 4;
  grid-row: 4 /5;
  background-color: hsl(163, 41%, 45%, 0.9);
  color: #fff;
  border: 1px solid #000;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  margin-top: 2px;
  margin-bottom: 18px;
  box-shadow: 12px 12px 10px -8px rgba(0, 0, 0, 0.64);
}

#giving-info a {
  color: #fff;
}

#giving-info:hover {
  background-color: hsl(163, 41%, 45%);
  box-shadow: 15px 15px 10px -8px rgba(0, 0, 0, 0.5);
}

.info {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.info h3 {
  margin: 0 auto;
  font-weight: 500;
}

.info a {
  margin: auto;
}

.hero-container {
  width: 50%;
  margin: auto;
  margin-top: -100px;
}

@media screen and (max-width: 769px) {
  #home {
    display: inline-block;
  }

  #gathering-info {
    border: 1px #333 solid
  }

  #mission-info {
    border: 1px #333 solid;
    border-top: 0;
  }

  #giving-info {
    margin-top: 0;
    border-top: 0
  }
  .hero-container {
    margin: auto;
  }
}

@media screen and (min-width: 1025px) {
  .hero-container {
    margin-top: -180px;
  }
}
.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center
}

#connect {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#connect h2 {
 font-weight: 300;
 margin: auto;
 padding: 20px
}

#connect a {
  color: #000;
  padding: 5px 5px 10px 50px;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
}

#connect a:visited {
  color: #000;
}

#connect a:hover,
#connect a:focus {
  color: #44a187;
}

#gathering {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
 
}

#gathering h2 {
  font-weight: 200
}

#meeting {
  padding: 0 30px;
  font-weight: 200;
  line-height: 1.5
}

@media screen and (max-width: 769px) {
  #connect a {
    color: #44a187;
    padding: 5px 20px;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
  }  
  #connect h2 {
    padding: 20px;
  }
}
#statementoffaith {
  display: -webkit-flex;
  display: flex;
  background-color: #f0f0f0;
}

#statement {
  margin-left: auto;
  margin-right: auto;
}

